/* ------------------------------------------------------------------ */
/* b. Header Styles
/* ------------------------------------------------------------------ */

header {
  position: relative;
  height: 800px;
  min-height: 500px;
  width: 100%;
  background: #161415;
  background-image: url(/portfolio_website/static/media/header-background.6fb8cd7e.jpg);
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
}

/* vertically center banner section */
header:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.banner {
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
}

.banner-text {
  width: 100%;
}

.banner-text h1 {
  font: 90px/1.1em 'opensans-bold', sans-serif;
  color: #fff;
  letter-spacing: -2px;
  margin: 0 auto 18px auto;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, .8);
  font-family: "Palatino Linotype";
}

.banner-text h3 {
  font: 18px/1.9em 'librebaskerville-regular', serif;
  color: #A8A8A8;
  margin: 0 auto;
  width: 70%;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, .5);
}

.banner-text h3 span,
.banner-text h3 a {
  color: #fff;
}

header .banner-text hr {
  width: 60%;
  margin: 18px auto 24px auto;
  border-color: #2F2D2E;
  border-color: rgba(150, 150, 150, .1);
}

/* header social links */
.social {
  margin: 24px 0;
  padding: 0;
  font-size: 30px;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, .8);
}

.social li {
  display: inline-block;
  margin: 0 15px;
  padding: 0;
}

.social li a {
  color: #fff;
}

.social li a:hover {
  color: #11ABB0;
}

/* scrolldown link */
.scrolldown a {
  position: absolute;
  bottom: 30px;
  left: 50%;
  margin-left: -29px;
  color: #fff;
  display: block;
  height: 42px;
  width: 42px;
  font-size: 42px;
  line-height: 42px;
  color: #fff;
  border-radius: 100%;
  transition: all .3s ease-in-out;
}

.scrolldown a:hover {
  color: #11ABB0;
}

/* primary navigation
--------------------------------------------------------------------- */

#nav-wrap {
  margin: auto;
  padding: 0;
  border: none;
  outline: none;
  text-align: fixed;
  float: center;
}



.flex-control-nav {
  text-align: center;
  margin-left: -30px;
}

/* nav-wrap */
#nav-wrap {
  font: 12px 'opensans-bold', sans-serif;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  margin: auto;
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
}

.opaque {
  background-color: #333;
}

/* hide toggle button */
#nav-wrap>a.mobile-btn {
  display: none;
}

#nav {
  min-height: 48px;
  width: auto;
  margin: auto;

  /* center align the menu */
  text-align: center;
}

#nav li {
  position: relative;
  list-style: none;
  height: 48px;
  display: inline-block;
}

/* Links */
#nav li a {

  /* 8px padding top + 8px padding bottom + 32px line-height = 48px */

  display: inline-block;
  padding: 8px 13px;
  line-height: 32px;
  text-decoration: none;
  text-align: center;
  color: #fff;
  transition: color .2s ease-in-out;
}

#nav li a:active {
  background-color: transparent !important;
}

#nav li.current a {
  color: #F06000;
}

